import React, {Component} from 'react';
import './App.css';
import {Link } from "react-router-dom";
import Song from './songs/The Journey Begins/The Journey Begins.mp4';
import Song2 from './songs/The Journey Begins/Where Land Meets Water.mp4';
import Song3 from './songs/The Journey Begins/The Affliction.mp4';
import Song4 from './songs/The Journey Begins/The Victory.mp4';
import Song5 from './songs/The Journey Begins/The Journey Home.mp4';
import Background from './background.jpg';

class Unknown extends Component{
  render(){
    return (
      <div>
		{/* Nav */}
		<nav class="navbar navbar-expand-lg navbar-dark left">
  <div class="container-fluid">
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
      <div class="navbar-nav">
	  <li>
		  <Link to="/">
			  <h5>Home</h5>
		  </Link>
	  </li>
	  <li>
		  <Link to="/journey">
			  <h5>The Journey Begins</h5>
		  </Link>
	  </li> 
	  <li>
		  <Link to="/since">
			  <h5>Since When</h5>
		  </Link>
	  </li>
	  <li>
		  <Link to="/without">
			  <h5>Without You</h5>
		  </Link>
	  </li>
      </div>
    </div>
  </div>
</nav>

    {/* Header */}
    <header id="header">
        <img src={Background} alt="BackgroundImage" />
        <h1 className="centered">Justin Townsend Official</h1>
		<h2 className="headtext">The Journey Begins</h2>
	</header>

	{/* About */}
		<div className="left">
			<h2>The Journey Begins</h2>
			<p>The Journey Begins is my fourth album. The album consist of 5 songs each with the goal to bring a sense of adventure to your life, life is short, live it to the fullest, and live while you still can.</p>
		</div>

		{/* Main */}
			<div id="main">

				<section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>The Journey Begins</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1 center">
					<div className="inner">
						<header className="align-center">
							<h1>Where Land Meets Water</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song2}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>The Affliction</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song3}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1 center">
					<div className="inner">
						<header className="align-center">
							<h1>The Victory</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song4}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>The Journey Home</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song5}></video>
							</div>
						</div>
					</div>
				</section>
			</div>

		{/* Footer */}
			<footer id="footer">
				<div className="copyright center">
					<ul className="icons">
						<li><a target="blank" href="https://www.youtube.com/channel/UCaDHVTA1pSE8pMsDaj1juSw">YouTube</a></li>
						<li><a target="blank" href="https://www.facebook.com/justin.townsend.7330763">Facebook</a></li>
						<li><a target="blank" href="https://www.instagram.com/im_justin_time/">Instagram</a></li>
					</ul>
					&copy; Untitled. Images: <a href="https://unsplash.com">Coverr</a>.
				</div>
			</footer>
      </div>
    );
  }
}

export default Unknown;
